@mixin sizeFluid($property, $f-min, $f-max, $v-min: 1280, $v-max: 1920, $base: 0){
	#{$property}: calcFluid($f-min, $f-max, $v-min, $v-max, $base);
	@media (min-width: #{$v-max}px) {
		#{$property}: #{$f-max}px;
	}
}


@mixin h0{
	font-family: "Moniqa", sans-serif;
	font-weight: 600;
	font-size: calcRem(80);
	line-height: 1.11;
	letter-spacing: 0.045em;
	text-transform: uppercase;
}
@mixin h1{
	font-family: "Moniqa", sans-serif;
	font-weight: 600;
	font-size: calcRem(66);
	line-height: 1.04;
	letter-spacing: 0.045em;
	text-transform: uppercase;
}
@mixin h2{
	font-family: "Moniqa", sans-serif;
	font-weight: 600;
	font-size: calcRem(30);
	line-height: 1.04;
	letter-spacing: 0.045em;
	text-transform: uppercase;
}
@mixin h3{
	font-weight: 700;
	font-size: calcRem(22);
	line-height: 1.3;
}
@mixin h4{
	font-weight: 700;
	font-size: calcRem(18);
	line-height: 1.3;
}
@mixin text12 {
	font-size: calcRem(12);
	line-height: 1.3;
}
@mixin text14 {
	font-size: calcRem(14);
	line-height: 1.3;
}
@mixin text16 {
	font-size: calcRem(16);
	line-height: 1.3;
}
@mixin text14-bold {
	@include text14;
	font-weight: 700;
}
@mixin text16-bold {
	@include text16;
	font-weight: 700;
}